				'<h2>一、帐号注册</h2>' +
				'<p>1、用户在使用本服务前需要注册一个&ldquo;阳光置业&rdquo;帐号。&ldquo;阳光置业&rdquo;帐号应当使用手机号码绑定注册，请用户使用尚未与&ldquo;阳光置业&rdquo;帐号绑定的手机号码，以及未被阳光置业根据本协议封禁的手机号码注册&ldquo;阳光置业&rdquo;帐号。阳光置业可以根据用户需求或产品需要对帐号注册和绑定的方式进行变更，而无须事先通知用户。</p>' +
				'<p>3、鉴于&ldquo;阳光置业&rdquo;帐号的绑定注册方式，您同意阳光置业在注册时将使用您提供的手机号码及/或自动提取您的手机号码及自动提取您的手机设备识别码等信息用于注册。您同意给予运营商授权，授权运营商有权自动提取您的手机号码进行认证并用于&ldquo;阳光置业&rdquo;账号注册，您保证遵守运营商的相关服务条款（<a href="https://www.immomo.com/service_terms.html">点击查看服务条款</a>），如运营商对您的手机号认证成功，则您的注册即完成。如您不同意对运营商的授权和/或服务条款或者是您的手机号认证失败，您可以手动修改运营商提取的手机号码，采取验证码方式进行注册登录。</p>' +
