				'<p>4、阳光置业未经用户同意不向任何第三方公开、 透露用户个人隐私信息。但以下特定情形除外：</p>' +
				'<p>(1) 阳光置业根据法律法规规定或有权机关的指示提供用户的个人隐私信息；</p>' +
