				'<p>6、阳光置业重视对未成年人的保护。阳光置业将依赖用户提供的个人信息判断用户是否为未成年人。任何18岁以下的未成年人均不得注册帐号或使用本服务</p>' +
				'<p>7、因阳光置业提供的服务系基于地理位置提供的移动社交服务，用户确认，其地理位置信息为非个人隐私信息，用户成功注册&ldquo;阳光置业&rdquo;帐号视为确认授权阳光置业提取、公开及使用用户的地理位置信息。用户地理位置信息将作为用户公开资料之一，由阳光置业向其他用户公开以便阳光置业向用户提供基于地理位置的移动社交服务。如用户需要终止向其他用户公开其地理位置信息，可随时自行设置为隐身状态。</p>' +
