				'<p>9、阳光置业保证在合法、正当与必要的原则下收集、使用或者公开用户个人信息且不会收集与提供的服务无关的用户个人信息。</p>' +
				'<p>10、阳光置业十分注重保护用户的个人隐私，并制定了《阳光置业隐私权政策》（<a href="https://m.immomo.com/inc/privacy_policy.html">点击查看</a>），用户亦可以通过&ldquo;设置&rdquo;页面里的&ldquo;帮助&rdquo;来进行具体查看，用户确认并同意使用阳光置业提供的服务将被视为接受《阳光置业隐私权政策》。</p>' +
				'<h2>四、内容规范</h2>' +
				'<p>1、本条所述内容是指用户使用本服务过程中所制作、上载、复制、发布、传播的任何内容，包括但不限于帐号头像、名称、用户说明等注册信息及认证资料，或文字、语音、图片、视频、图文等发送、回复或自动回复消息和相关链接页面，以及其他使用帐号或本服务所产生的内容。</p>' +
				'<p>2、用户不得利用&ldquo;阳光置业&rdquo;帐号或本服务制作、上载、复制、发布、传播如下法律、法规和政策禁止的内容：</p>' +
